<template>
  <div class="pos">
    <el-row>
      <el-col :span='7' class="posOrder" id="posOrderList">
       <el-tabs>
         <el-tab-pane label="点餐">
           <el-table :data='tabelData' border style="width:100%;">
             <el-table-column prop="goodsName" label="商品名称"></el-table-column>
             <el-table-column prop="count" label="数量" width="70"></el-table-column>
             <el-table-column prop="price" label="金额" width="70"></el-table-column>
             <el-table-column  label="操作" width="100" fixed="right">
              <template slot-scope="scope">
                <el-button type="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                <el-button type="text" size="small" @click="removeOneGoods(scope.row)">删除</el-button>
              </template>
             </el-table-column>
           </el-table>
           <div class="totalCount">
             数量：{{totalNum}} 金额：{{totalMoney}} 元
           </div>
           <div>
             <el-button type="warning">挂单</el-button>
             <el-button type="danger" @click="removeAllGoods()">删除</el-button>
             <el-button type="success" @click="checkout">结账</el-button>
           </div>
         </el-tab-pane>
         <el-tab-pane label="挂单">2</el-tab-pane>
         <el-tab-pane label="外卖">3</el-tab-pane>
       </el-tabs>
      </el-col>
      <el-col :span='17' class="oftenGoods">
         <div class="title">
           <div>常用商品</div>
           <div class="oftenGoodsList">
             <ul>
               <li v-for="good in oftenGoods" @click="addOrderList(good)">
                 <span>{{good.goodsName}}</span>
                 <span class="oftenPrice">￥{{good.price}}元</span>
               </li>
             </ul>
           </div>
         </div>
         <div class="goodsType">
           <el-tabs>
             <el-tab-pane label="汉堡">
               <ul class='cookList' >
                  <li v-for="good in type0Goods" @click="addOrderList(good)">
                      <span class="foodImg"><img :src="good.goodsImg" width="100%"></span>
                      <span class="foodName">{{good.goodsName}}</span>
                      <span class="foodPrice">￥{{good.price}}元</span>
                  </li>
              </ul>
             </el-tab-pane>
             <el-tab-pane label="小食">
               <ul class='cookList' >
                  <li v-for="good in type1Goods" @click="addOrderList(good)">
                      <span class="foodImg"><img :src="good.goodsImg" width="100%"></span>
                      <span class="foodName">{{good.goodsName}}</span>
                      <span class="foodPrice">￥{{good.price}}元</span>
                  </li>
              </ul>
             </el-tab-pane>
             <el-tab-pane label="饮料">
               <ul class='cookList' >
                  <li v-for="good in type2Goods" @click="addOrderList(good)">
                      <span class="foodImg"><img :src="good.goodsImg" width="100%"></span>
                      <span class="foodName">{{good.goodsName}}</span>
                      <span class="foodPrice">￥{{good.price}}元</span>
                  </li>
              </ul>
             </el-tab-pane>
             <el-tab-pane label="套餐">
               <ul class='cookList' >
                  <li v-for="good in type3Goods" @click="addOrderList(good)">
                      <span class="foodImg"><img :src="good.goodsImg" width="100%"></span>
                      <span class="foodName">{{good.goodsName}}</span>
                      <span class="foodPrice">￥{{good.price}}元</span>
                  </li>
              </ul>
             </el-tab-pane>
           </el-tabs>
         </div>
      </el-col>  
    </el-row>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "Pos",
  data() {
    return {
      tabelData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalNum: 0,
      totalMoney: 0
    };
  },
  created: function() {
    axios
      .get("http://jspang.com/DemoApi/oftenGoods.php")
      .then(response => {
        this.oftenGoods = response.data;
        // console.log(response);
      })
      .catch(error => {
        alert("网络错误！");
      });
    axios
      .get("http://jspang.com/DemoApi/typeGoods.php")
      .then(response => {
        this.type0Goods = response.data[0];
        this.type1Goods = response.data[1];
        this.type2Goods = response.data[2];
        this.type3Goods = response.data[3];
      })
      .catch(error => {
        alert("网络错误！");
      });
  },
  mounted: function() {
    var orderListHeight = document.body.clientHeight;
    document.getElementById("posOrderList").style.height =
      orderListHeight + "px";
  },
  methods: {
    addOrderList(goods) {
      let isHave = false;
      for (let i = 0; i < this.tabelData.length; i++) {
        if (this.tabelData[i].goodsId == goods.goodsId) {
          isHave = true;
        }
      }
      if (isHave) {
        let arr = this.tabelData.filter(e => e.goodsId == goods.goodsId);
        arr[0].count++;
      } else {
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tabelData.push(newGoods);
      }
      this.getMoneyAndNum();
    },
    removeOneGoods(goods) {
      this.tabelData = this.tabelData.filter(e => e.goodsId != goods.goodsId);
      this.getMoneyAndNum();
    },
    getMoneyAndNum() {
      this.totalNum = 0;
      this.totalMoney = 0;
      if (this.tabelData) {
        this.tabelData.forEach(e => {
          this.totalNum += e.count;
          this.totalMoney += e.count * e.price;
        });
      }
    },
    removeAllGoods() {
      this.tabelData = [];
      this.totalNum = 0;
      this.totalMoney = 0;
    },
    checkout() {
      if (this.totalMoney != 0) {
        this.tabelData = [];
        this.totalNum = 0;
        this.totalMoney = 0;
        this.$message({
          message: "结账成功，感谢你又为店里出了一份力!",
          type: "success"
        });
      } else {
        this.$message.error("不能空结。老板了解你急切的心情！");
      }
    }
  }
};
</script>

<style scoped>
.posOrder {
  background-color: #f9f9f9;
  border-right: 1px solid silver;
  padding: 0 10px;
}
.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
}
.oftenGoodsList ul li {
  list-style: none;
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 5px;
  background-color: #fff;
  cursor: pointer;
}
.oftenPrice {
  color: #58b7ff;
}
.goodsType {
  clear: both;
  padding-left: 20px;
}
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
}
.cookList li span {
  display: block;
  float: left;
  cursor: pointer;
}
.foodImg {
  width: 40%;
}
.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.totalCount {
  background: #d3dce6;
  border-bottom: 1px solid rebeccapurple;
  width: 100%;
  margin: 20px 0;
  padding: 20px;
  font-size: 20px;
}
</style>



